import React from "react";
import Helmet from "react-helmet";
import PostListing from "../components/PostListing/PostListing";
import HomeHeader from "../components/HomeHeader/HomeHeader";
import FreeBook from "../components/HomeBookCard/FreeBook";
import CompleteBook from "../components/HomeBookCard/CompleteBook";
import HomeBookCard from "../components/HomeBookCard/HomeBookCard";
import SubscriptionBar from "../components/SubscriptionBar/SubscriptionBar";
import SEO from "../components/SEO/SEO";
import config from "../../data/SiteConfig";

import "./index.scss";

class Index extends React.Component {
  render() {
    const postEdges = this.props.data.allMarkdownRemark.edges;

    return (
      <div className="index-container">
        <Helmet title={config.siteTitle} />
        <SEO postEdges={postEdges} />
        <HomeHeader />

        <main className="index-main-container">
          <section className="index-main-card-area" id="subscription">

            <HomeBookCard>
              <FreeBook />
            </HomeBookCard>
            
            <HomeBookCard>
              <CompleteBook />
            </HomeBookCard>

          </section>

          <h1 className="index-recent-posts-title">Recent posts</h1>

          <PostListing postEdges={postEdges} />

          <footer className="sub-bar-active">
            <section className="footer-about-us">
              <div className="footer-light-bulb"><img src="webondevices-logo.png" alt="light bulb logo" /></div>
              <h2 className="footer-title">Web on Devices</h2>
              <p className="footer-main-message">Electronics Hacking with Javascript<br />and other Web Technologies</p>
              <a className="connect twitter" href="https://twitter.com/web_on_devices">Twitter</a>
              <a className="connect facebook" href="https://www.facebook.com/webondevices">Facebook</a>
            </section>

            <section className="about-author">
              <img className="author" src="matemarschalko.jpg" alt="Mate Marschalko" />
              <h2>Mate Marschalko</h2>
              <p className="about-me">Web Developer, Creative Technologist and Maker. Builds Internet connected devices for the Internet of Things.</p>
            </section>

            <section className="legal">
              All rights reserved | Contact at <a href="emailto: hello@webondevices.com">hello@webondevices.com</a>
            </section>
          </footer>

          <SubscriptionBar />

        </main>

      </div>
    );
  }
}

export default Index;

/* eslint no-undef: "off"*/
export const pageQuery = graphql`
  query IndexQuery {
    allMarkdownRemark(
      limit: 2000
      sort: { fields: [frontmatter___date], order: DESC }
    ) {
      edges {
        node {
          fields {
            slug
          }
          excerpt
          timeToRead
          frontmatter {
            title
            tags
            cover
            date
          }
        }
      }
    }
  }
`;



